@charset "utf-8";

// 스프라이트
@import 'import/sprite_maps';
@import 'import/sprite_mixin';

// 컴포넌트 
@import 'component/reset.scss';
@import 'component/layout.scss';
@import 'component/header.scss';
@import 'component/aside.scss';
@import 'component/content.scss';

// 반응형
@import 'component/responsive.scss';
$color-accent-blue: #2288f8;

body,
input,
textarea,
select,
button,
table {
    font-size: 21px;
    line-height: 2;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
    font-weight: normal;
    color: #424242;
}

ul li {
    list-style-type: none;
}

html,
body {
    height: 100%;
}

input:focus {
    outline: none !important;
}

// 폰트
// Montserrat - 사이드바 헤딩, 메뉴 gnb, 티오씨
@font-face {
    font-family: "Montserrat";
    font-weight: 100 900;
    src: url("src/fonts/Montserrat-VariableFont_wght.ttf");
}

// Libre Franklin - tag
@font-face {
    font-family: "Libre Franklin";
    font-weight: 100 900;
    src: url("src/fonts/LibreFranklin-VariableFont_wght.ttf");
}

// Inter Tight - 헤더 서브슬로건
@font-face {
    font-family: "Inter Tight";
    font-weight: 100 900;
    src: url("src/fonts/InterTight-VariableFont_wght.ttf");
}

// Urbanist - 말풍선스타일
@font-face {
    font-family: "Urbanist";
    font-weight: 100 900;
    src: url("src/fonts/Urbanist-VariableFont_wght.ttf");
}

// Noto Emoji - 본문 사이드노트
@font-face {
    font-family: "Noto Color Emoji";
    font-weight: 400;
    src: url("src/fonts/noto-color-emoji_normal_400.ttf");
}

body {
    border: 2px solid blue;
}