$color-post-body-text: #676767;
$color-post-title-text: #424242;
$color-accent-blue: #2288f8;
$color-light-grey: #bebebe;
$font-montserrat: 'Montserrat', sans-serif;
$font-intertight: 'Inter Tight', sans-serif;
$font-urbanist: 'urbanist', sans-serif;
$font-emoji: 'Noto Color Emoji';

.content-wrap {
    padding: 26px 0 26px;
    display: flex;
    flex-direction: column;
}

.content-inner-box {
    border-radius: 20px;
    padding: 50px;
    background: #fff;

    &+& {
        margin-top: 26px;
    }

    &>.wp-block-query,
    &>.wp-block-template-part {
        max-width: 620px;
        margin-left: auto !important;
        margin-right: auto !important;
        // --wp--style--global--content-size: 620px;
    }
}

// [/g] 'text-bubble' 말풍선스타일
.wp-block-group.text-bubble {
    position: relative;
    margin: 60px 9% 190px 9%;
    padding: 50px 37px;
    font-family: $font-urbanist;
    font-size: 23px;
    letter-spacing: -0.2px;
    font-weight: 300;
    line-height: 1.8;
    color: #fcfcfc;
    background: $color-accent-blue;
    border-radius: 34px;

    p {
        margin: 0;
    }

    &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -11px;
        margin-left: -6px;
        width: 0;
        height: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
        border-top: 12px solid $color-accent-blue;
    }

    &:before {
        content: "";
        position: absolute;
        bottom: -150px;
        left: 50%;
        margin-left: -65px;
        display: block;
        width: 130px;
        height: 130px;
        background: #f8f8f8;
        border-radius: 80px;
        background-size: 130px 130px;
    }

    &.celebrate:before {
        background-image: url(../img/set-memoji/memoji-celebrate.png);
    }

    &.dizzy:before {
        background-image: url(../img/set-memoji/memoji-dizzy.png);
    }

    &.neh:before {
        background-image: url(../img/set-memoji/memoji-neh.png);
    }

    &.secret:before {
        background-image: url(../img/set-memoji/memoji-secret.png);
    }

    &.shh:before {
        background-image: url(../img/set-memoji/memoji-shh.png);
    }

    &.shock:before {
        background-image: url(../img/set-memoji/memoji-shock.png);
    }

    &.sick:before {
        background-image: url(../img/set-memoji/memoji-sick.png);
    }

    &.smash:before {
        background-image: url(../img/set-memoji/memoji-smash.png);
    }

    &.think:before {
        background-image: url(../img/set-memoji/memoji-think.png);
    }

    &.ugh:before {
        background-image: url(../img/set-memoji/memoji-ugh.png);
    }

    &.wink:before {
        background-image: url(../img/set-memoji/memoji-wink.png);
    }
}

// [/g] 'text-sidenote' 스타일
.wp-block-group.text-sidenote {
    margin: 50px auto !important;
    box-shadow: 0 0 11px 0 rgba(0, 0, 0, 0.07);
    border-radius: 40px;
    color: #474747;

    .wp-block-heading {
        padding: 30px 7% 28px;
        margin-top: 0;
        font-family: $font-urbanist;
        font-size: 28px;
        letter-spacing: 0;
        line-height: 1.6;
        border-bottom: 1px solid #eaeaea;

        &:before {
            content: '📌';
            font-family: $font-emoji;
            display: inline-block;
            padding-right: 10px;
        }
    }

    p {
        margin-top: 0;
        padding: 32px 7% 38px;
    }
}

// 타이틀
.wp-block-post-title {
    font-family: $font-intertight;
    font-weight: 500;
    font-size: 36px;
    line-height: 1.8;
    color: $color-post-title-text;

    a {
        text-decoration: none;
        color: $color-post-title-text;
    }
}

// 본문
.content-inner-box {
    font-size: 22px;
    font-weight: 300;
    color: $color-post-body-text;

    // 태그(링크)
    .taxonomy-post_tag {
        margin: 34px 0 36px;

        a {
            display: inline-block;
            font-family: $font-intertight;
            padding: 2px 16px 2px;
            margin: 0 5px 5px 0;
            font-size: 20px;
            font-weight: 300;
            text-decoration: none;
            color: $color-light-grey;
            border: 1px solid #eaeaea;
            border-radius: 4px;

            @media (hover: hover) and (pointer: fine) {
                &:hover {
                    color: #242424;
                    border-color: #474747;
                }
            }
        }
    }

    .wp-block-post-terms__separator {
        color: transparent;
        font-size: 0;
    }

    // 이미지
    .wp-block-image {
        display: inline-block;
        margin-top: 62px;
        width: 100%;
        text-align: center;
        overflow: hidden;
        border-radius: 4px;

        img {
            display: inline-block;
            vertical-align: top;
        }
    }

    // 서브헤딩
    .wp-block-heading {
        font-family: $font-intertight;
        font-size: 30px;
        font-weight: 400;
        margin-top: 50px;
        color: $color-post-title-text;
    }

    // 본문
    .entry-content {
        margin-bottom: 84px;
    }

    // 날짜
    .wp-block-post-date {
        display: inline-block;

        a {
            font-family: $font-intertight;
            font-size: 20px;
            font-weight: 300;
            color: $color-post-body-text;
            text-decoration: none;
            pointer-events: none;
            cursor: default;
            color: $color-light-grey;
        }
    }

    // 카데고리(링크)
    .taxonomy-category {
        display: inline-block;
        margin-left: 8px;

        a {
            font-family: $font-urbanist;
            font-size: 21px;
            font-weight: normal;
            text-transform: capitalize;
            color: $color-accent-blue;
            border-radius: 2px;
            padding: 6px;
            text-decoration: none;

            @media (hover: hover) and (pointer: fine) {
                &:hover {
                    background-color: $color-accent-blue;
                    color: #fff;
                }
            }
        }
    }

    table {
        color: $color-accent-blue;
    }
}

// 타이틀하위 본문 내 요소
.wp-block-post-content {

    p {
        margin: 40px 0;
    }

    ul {
        margin: 42px 0 54px;

        li {
            &:before {
                content: '-';
                padding: 0 4px 0 2px;
            }
        }

        li~li {
            margin-top: 13px;
        }
    }
}

// 인스타/트위터 임베드
.instagram-media-rendered,
.twitter-tweet-rendered {
    margin: 14px auto !important;
}

// 티오씨스타일
.wp-block-aioseo-table-of-contents {
    margin: 65px auto !important;
    box-shadow: 0 0 11px 0 rgba(0, 0, 0, 0.07);
    border-radius: 40px;
    text-align: center;

    &+.wp-block-heading {
        margin-top: 65px;
    }

    &:before {
        content: 'Table of Contents';
        display: block;
        padding: 32px 14px 27px;
        font-family: $font-urbanist;
        font-size: 28px;
        font-weight: 400;
        letter-spacing: -0.4px;
        color: $color-post-title-text;
        border-bottom: 1px solid #eaeaea;
    }

    ul {
        counter-reset: my-counter;
        margin: 0;
        line-height: 1.8;
        padding: 24px 14px 38px;

        li {
            position: relative;
            padding-left: 10px;
            display: table;
            margin: 0 auto;
            max-width: 84%;

            &:before {
                content: counter(my-counter) ".";
                counter-increment: my-counter;
                position: absolute;
                left: -25px;
                top: 50%;
                margin-top: -16px;
                min-width: 40px;
                min-height: 40px;
                font-family: $font-urbanist;
                font-size: 26px;
                font-weight: 500;
                color: $color-accent-blue;
            }
        }

        li~li {
            margin-top: 0;

        }

        a {
            display: inline-block;
            text-decoration: none;
            margin-top: 13px;
            word-wrap: break-word;
            font-family: $font-urbanist;
            font-size: 24px;
            font-weight: 500;
            line-height: 2.2;
            letter-spacing: -0.2px;
            color: $color-accent-blue;

            &:hover {
                text-decoration: underline;
            }
        }
    }
}

// 코멘트
.wp-block-comments {
    margin-top: 80px;
}

.comment-reply-title {
    font-family: $font-intertight;
    font-size: 34px;
    font-weight: 200;
    letter-spacing: -0.5px;
    text-transform: uppercase;
    color: #d4d4d4;
}

.required-field-message,
.logged-in-as {
    display: none;
}