$font-intertight: 'Inter Tight', sans-serif;

body>.wp-site-blocks {
    height: 100%;
    display: flex;
    flex-direction: row;
    padding: 0;
    background: #f9f9f9;
    overflow: scroll;

    &>.content-wrap {
        flex: 1;
        flex-basis: 0;
    }
}

header.wp-block-template-part {
    height: 100%;
}

.wp-site-blocks>*+* {
    margin-block-start: 0;
}

.index-page {
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow: scroll;
}

// 서치
.search-wrap {
    position: relative;
    margin-top: 24px;
}

.search-input {
    display: block;
    padding: 0 84px;
    height: 80px;
    width: 100%;
    font-family: $font-intertight;
    border-radius: 20px;
    border: none;

    &::placeholder {
        color: #cdcdcd;
    }
}

.search-button {
    position: absolute;
    top: 0;
    left: 0;
    width: 74px;
    height: 74px;
    margin: 6px;
    color: transparent;
    cursor: pointer;

    &:before {
        content: '';
        position: absolute;
        display: block;
        top: 18%;
        left: 20%;
        width: 45%;
        height: 45%;
        border-radius: 50%;
        border-width: 4px;
        border-style: solid;
        border-color: #cecece;
    }

    &:after {
        content: '';
        position: absolute;
        bottom: 22.5%;
        right: 23.5%;
        width: 30%;
        height: 4px;
        margin-top: -2px;
        background-color: #cecece;
        transform: rotate(45deg);
        transform-origin: right bottom;
    }
}

.search-wrap.is_focus .delete-icon {
    position: absolute;
    top: 50%;
    right: 30px;
    width: 26px;
    height: 26px;
    transform: translateY(-50%);
    cursor: pointer;

    &:before,
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        margin-left: 15px;
        display: inline-block;
        width: 2px;
        background-color: #474747;
    }

    &:before {
        transform: rotate(-45deg);
    }

    &:after {
        transform: rotate(45deg);
    }
}